<template>
  <div id="app">
    <main-background
      :status="status"
    ></main-background>
    <!-- <button @click="status = 'top'">top</button>
    <button @click="status = 'mid'">mid</button>
    <button @click="status = 'bottom'">bottom</button> -->
    <div class="top-space "
      :class="actionClass"
    >

    </div>
    <ul class="todos " :class="actionClass">
      <li class="todos__tile"  @click="item.done = !item.done" v-for="(item, key) in todos" :key="key">
        <div class="tile__btn" :class="{active: item.done}"></div>
        <div class="tile__text">{{ item.text }}</div>
      </li>
    </ul>
    <div class="toolbar " :class="actionClass">
      <img src="@/assets/add.svg" v-show="status === 'mid'">
      <img src="@/assets/pause.svg" v-show="status !== 'mid'" @click="status = 'bottom'">
      <img src="@/assets/stop.svg" v-show="status !== 'mid'" @click="status = 'mid'">
      <img src="@/assets/play.svg" v-show="status === 'mid'" @click="status = 'top'" >
      <img src="@/assets/Settings.svg" v-show="status === 'mid'">
    </div>
  </div>
</template>

<script>

import MainBackground from '@/components/MainBackground.vue'
export default {
  components: {
    MainBackground
  },
  data () {
    return {
      status: 'mid',
      todos: [{
        text: '棒式', done: false
      }, {
        text: '側棒式', done: true
      }, {
        text: '側棒式', done: false
      }, {
        text: '他們所有的設備和儀器彷彿都是有生命的。', done: false
      }, {
        text: '棒式', done: false
      }, {
        text: '側棒式', done: true
      }, {
        text: '側棒式', done: false
      }, {
        text: '他們所有的設備和儀器彷彿都是有生命的。', done: false
      }]
    }
  },
  computed: {
    actionClass () {
      console.log({
        mid: this.status === 'mid',
        top: this.status === 'top',
        bottom: this.status === 'bottom'
      })
      return {
        mid: this.status === 'mid',
        top: this.status === 'top',
        bottom: this.status === 'bottom'
      }
    }
  }
}
</script>

<style lang="stylus">
@font-face
  font-family no1
